import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { UtilsService } from '../../service/utils.service';
import { Router } from '@angular/router';
import { catchError } from 'rxjs/operators';
import { AuthenticationService } from '../../service/authentication.service';

@Component({
  selector: 'app-otp-password-verification',
  templateUrl: './otp-password-verification.component.html',
  styleUrls: ['./otp-password-verification.component.css']
})
export class OtpPasswordVerificationComponent implements OnInit {

  otpform: FormGroup;
  submitted = false;

  constructor(
    private formBuilder: FormBuilder, 
    private auth: AuthenticationService, 
    private toastr : ToastrService,
    private router: Router
  ) { }

  ngOnInit() {
    this.otpform = this.formBuilder.group({
      otp1: ['', [Validators.required]],
      otp2: ['', [Validators.required]],
      otp3: ['', [Validators.required]],
      otp4: ['', [Validators.required]]
    });
  }

  // convenience getter for easy access to form fields
  get f() { return this.otpform.controls; }
 
    verifyOtp(){
      this.submitted = true;

      // stop here if form is invalid
      if (this.otpform.invalid) {
        return;
      }

      let otp = this.otpform.value.otp1 + this.otpform.value.otp2 + this.otpform.value.otp3 + this.otpform.value.otp4 
      console.log(otp)
      let data = {
        otp_value: otp
      }
      this.auth.verifyPasswordOtp(data).subscribe(
        res=> {
          this.toastr.success(res["message"]);
          localStorage.setItem('access_token', res['access_token'])
          // this.successMessage = res['message']
          this.router.navigateByUrl("/forgot-password")
          // this.router.navigateByUrl("/forgot-password")
          // this.router.navigateByUrl("/home/dashboard")
          // this.router.navigateByUrl("/store-form-details")
        },
        error=> {
          console.log(error);
          // this.invalidLogin = true
          // this.errorMessage = error.error.message
          this.toastr.error(error.error.message);
        }
      );
    }

    resendOtp(){
      this.auth.resendOtp().subscribe(
        res=> {
          console.log(res["message"]);
          this.toastr.success(res["message"]);
          localStorage.setItem('access_token', res['access_token'])
          // this.successMessage = res['message']
        },
        error=> {
          console.log(error);
          // this.invalidLogin = true
          // this.errorMessage = error.error.message
          this.toastr.error(error.error.message);
        }
      );
    }
     
    keyPress(e: any) {
      console.log(e)
      console.log(e.target.value);
      if(e.code=='Backspace'){
      var target = e.srcElement || e.target;
      target.nextElementSibling.focus();
      }
      
      var target = e.srcElement || e.target;
      var maxLength = parseInt(target.attributes["maxlength"].value, 10);
      var myLength = target.value.length;
      if (maxLength == 1) {
          var next = target;
          while (next = next.nextElementSibling) {
              if (next == null)
                  break;
              if (next.tagName.toLowerCase() === "input") {
                  next.focus();
                  break;
              }
          }
      }
      // Move to previous field if empty (user pressed backspace)
      else if (myLength === 0) {
          var previous = target;
          while (previous = previous.previousElementSibling) {
              if (previous == null)
                  break;
              if (previous.tagName.toLowerCase() === "input") {
                  previous.focus();
                  break;
              }
          }
      }
    }
    
    movetoNext(current, id) {
      var phoneno = /^\d{1}$/;
      if (current.key == 'Backspace' && current.target.value == '' && id != 1) {
        id -= 1
        this.changeColor('#337B94' , 4)
        document.getElementById(id).focus();
    
      }
      if (phoneno.test(current.target.value)) {
    
        if (id <= 3 && current.key != 'Backspace' && current.key != 'ArrowLeft' && current.key != 'Tab') {
          id += 1
          document.getElementById(id).focus();
        }
      }
    }

    changeColor(color , id){
      for(let i = 1; i <= id ; i++){
        document.getElementById(i.toString()).style.color = color
        document.getElementById(i.toString()).style.borderBottomColor = color
      }
    }

    sendEmailVerification(){
      this.auth.sendEmailVerification().subscribe(
        res=> {
          console.log(res["message"]);
          this.toastr.success(res["message"]);
        },
        error=> {
          console.log(error);
          this.toastr.error(error.error.message);
        }
      );
    }

}
